import React from 'react';
import { Link } from 'react-router-dom';
import './KairouanPage.css';
import okbaImage from '../assets/okba 2.jpg';
import medinaImage from '../assets/medinakairouan.jpg';
import sidiSahbiImage from '../assets/sidisahbi.jpg';
import bassinImage from '../assets/bassin.jpg';
import flagImage from '../assets/Flag.png';

const KairouanPage: React.FC = () => {
  const attractions = [
    {
      id: 1,
      name: "Grande Mosque Oqba Ibn Nafi",
      description: "Premire mosque de l'Occident musulman, fonde en 670, vritable joyau de l'architecture islamique",
      image: okbaImage,
      highlights: [
        "Minaret le plus ancien du Maghreb",
        "Cour pave de marbre",
        "Salle de prire aux 414 colonnes",
        "Mihrab en faence de Kairouan"
      ],
      history: "Fonde par Oqba Ibn Nafi Al Fihri en 670 (50 de l'Hgire), cette mosque est considre comme l'une des plus importantes du monde musulman.",
      visitInfo: "Ouverte tous les jours sauf vendredi matin. Tenue vestimentaire respectueuse exige."
    },
    {
      id: 2,
      name: "Mdina de Kairouan",
      description: "Centre historique class UNESCO, labyrinthe de ruelles authentiques et de souks traditionnels",
      image: medinaImage,
      highlights: [
        "Remparts du 11me sicle",
        "Souks traditionnels",
        "Architecture andalouse",
        "Patrimoine UNESCO depuis 1988"
      ],
      history: "La mdina de Kairouan conserve son trac urbain d'origine avec ses remparts, ses portes monumentales et ses quartiers rsidentiels.",
      visitInfo: "Accessible librement. Visite guide recommande pour dcouvrir les trsors cachs."
    },
    {
      id: 3,
      name: "Zaoua Sidi Sahbi",
      description: "Mausole du Compagnon du Prophte, lieu de plerinage orn de magnifiques carreaux de faence",
      image: sidiSahbiImage,
      highlights: [
        "Tombeau d'Abou Djemaa El Balaoui",
        "Dcoration en faence polychrome",
        "Coupoles et minarets orns",
        "Lieu de plerinage majeur"
      ],
      history: "Sidi Sahbi, compagnon du Prophte Mohammed, est vnr  Kairouan. Son mausole est un chef-d'uvre de l'art dcoratif tunisien.",
      visitInfo: "Ouvert aux visiteurs. Respect des traditions religieuses demand."
    },
    {
      id: 4,
      name: "Bassins des Aghlabides",
      description: "Monuments hydrauliques du 9me sicle, tmoins du gnie technique de la dynastie aghlabide",
      image: bassinImage,
      highlights: [
        "Ingnierie hydraulique du 9me sicle",
        "Bassin polygonal unique",
        "Systme d'adduction d'eau",
        "Vue panoramique sur Kairouan"
      ],
      history: "Construits au 9me sicle sous la dynastie des Aghlabides, ces bassins alimentaient la ville en eau depuis les montagnes.",
      visitInfo: "Site ouvert toute l'anne. Idal pour admirer le coucher de soleil sur Kairouan."
    }
  ];

  return (
    <div className="kairouan-page">
      {/* Hero Section */}
      <div className="page-header">
        <div className="container">
          <div className="kairouan-title-container">
            <img src={flagImage} alt="Drapeau Tunisien" className="title-flag-bg" />
            <img src={okbaImage} alt="Grande Mosque de Kairouan" className="title-hero-img" />
            <div className="title-overlay">
              <h1>Kairouan</h1>
              <p>Ville sainte et premire capitale de l'Ifriqiya</p>
            </div>
          </div>
        </div>
      </div>

      {/* Introduction */}
      <section className="kairouan-intro section-padding">
        <div className="container">
          <div className="intro-content">
            <div className="intro-text">
              <h2>La Quatrime Ville Sainte de l'Islam</h2>
              <p>
                Fonde en 670 par Oqba Ibn Nafi, Kairouan fut la premire capitale de l'Ifriqiya 
                et le point de dpart de l'islamisation du Maghreb. Cette ville sainte, considre 
                comme la quatrime ville sainte de l'Islam aprs La Mecque, Mdine et Jrusalem, 
                abrite des trsors architecturaux uniques.
              </p>
              <p>
                Ses mosques, ses mdersas et sa mdina classe au patrimoine mondial de l'UNESCO 
                tmoignent de treize sicles d'histoire islamique. Kairouan est galement rpute 
                pour ses tapis, ses cuivres et ses ptisseries traditionnelles.
              </p>
            </div>
            <div className="intro-flag">
              <img src={flagImage} alt="Drapeau Tunisien" className="tunisia-flag" />
            </div>
          </div>
        </div>
      </section>

      {/* Attractions principales */}
      <section className="attractions section-padding">
        <div className="container">
          <div className="section-header text-center">
            <h2>Sites Incontournables</h2>
            <p>Dcouvrez les joyaux architecturaux et spirituels de Kairouan</p>
          </div>

          <div className="attractions-grid">
            {attractions.map((attraction, index) => (
              <div key={attraction.id} className="attraction-card" style={{ animationDelay: `${index * 0.2}s` }}>
                <div className="card-image">
                  <img src={attraction.image} alt={attraction.name} />
                  <div className="card-overlay">
                    <h3>{attraction.name}</h3>
                  </div>
                </div>
                
                <div className="card-content">
                  <p className="description">{attraction.description}</p>
                  
                  <div className="highlights">
                    <h4>Points d'intrt :</h4>
                    <ul>
                      {attraction.highlights.map((highlight, idx) => (
                        <li key={idx}>{highlight}</li>
                      ))}
                    </ul>
                  </div>
                  
                  <div className="history">
                    <h4>Histoire :</h4>
                    <p>{attraction.history}</p>
                  </div>
                  
                  <div className="visit-info">
                    <h4>Informations pratiques :</h4>
                    <p>{attraction.visitInfo}</p>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Informations pratiques */}
      <section className="practical-info section-padding bg-light">
        <div className="container">
          <h2>Prparez votre visite</h2>
          <div className="info-grid">
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Comment s'y rendre</h3>
              <ul>
                <li>160 km de Tunis (2h de route)</li>
                <li>50 km de Sousse (1h de route)</li>
                <li>Liaisons bus rgulires</li>
                <li>Parking disponible prs des sites</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Meilleure priode</h3>
              <ul>
                <li>Octobre  mai (temps clment)</li>
                <li>viter juillet-aot (trs chaud)</li>
                <li>Ramadan : horaires modifis</li>
                <li>Vendredi : mosques fermes le matin</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Code vestimentaire</h3>
              <ul>
                <li>Tenue dcente exige</li>
                <li>Bras et jambes couverts</li>
                <li>Chaussures faciles  retirer</li>
                <li>Foulard pour les femmes (mosques)</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Billets et tarifs</h3>
              <ul>
                <li>Grande Mosque : Entre gratuite</li>
                <li>Bassins des Aghlabides : Entre gratuite</li>
                <li>Mdina et Zaoua : Accs libre</li>
                <li>Visites guides sur demande</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* Call to action */}
      <section className="cta-section section-padding">
        <div className="container text-center">
          <h2>Planifiez votre visite  Kairouan</h2>
          <p>Dcouvrez la richesse spirituelle et architecturale de la ville sainte</p>
          <div className="cta-buttons">
            <Link to="/destinations" className="btn btn-primary">
              Autres destinations
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
};

export default KairouanPage;
