import React from 'react';
import { Link } from 'react-router-dom';
import './BizertePage.css';
// @ts-ignore
import heroImage from '../assets/vieuportbizerte.webp';
// @ts-ignore
import vieuxPortImage from '../assets/vieuportbizerte.webp';
// @ts-ignore
import forteEspagneImage from '../assets/bridgebizerte.jpg';
// @ts-ignore
import lacImage from '../assets/coucoubeachbizerte.webp';
// @ts-ignore
import corniImage from '../assets/bizertemedina.jpg';
import flagImage from '../assets/Flag.png';

const BizertePage: React.FC = () => {
  const attractions = [
    {
      name: "Vieux Port de Bizerte",
      description: "Cur historique de la ville, le vieux port de Bizerte sduit par son authenticit. Bord de maisons traditionnelles et de petits cafs, c'est le lieu idal pour observer les pcheurs rentrer avec leur prise quotidienne.",
      image: vieuxPortImage,
      highlights: ["Bateaux de pche traditionnels", "Architecture andalouse", "Cafs du port", "March aux poissons"]
    },
    {
      name: "Pont Mobile de Bizerte",
      description: "Emblme de la ville, ce pont historique relie la vieille ville au quartier moderne. Monument technique remarquable, il s'ouvre pour laisser passer les bateaux entre le lac et la mer.",
      image: forteEspagneImage,
      highlights: ["Monument historique", "Ingnierie unique", "Vue panoramique", "Liaison ville-port"]
    },
    {
      name: "Plage de Coucou",
      description: "Magnifique plage de sable fin situe prs du centre-ville, la plage de Coucou offre des eaux cristallines et une vue imprenable sur la cte mditerranenne de Bizerte.",
      image: lacImage,
      highlights: ["Sable fin dor", "Eaux cristallines", "Proximit centre-ville", "Activits nautiques"]
    },
    {
      name: "Mdina de Bizerte",
      description: "Centre historique authentique de Bizerte, la mdina prserve l'architecture traditionnelle tunisienne avec ses ruelles troites, ses souks colors et ses monuments anciens.",
      image: corniImage,
      highlights: ["Architecture traditionnelle", "Souks authentiques", "Monuments historiques", "Artisanat local"]
    }
  ];

  return (
    <div className="bizerte-page">
      {/* Hero Section */}
      <div className="page-header">
        <div className="container">
          <div className="bizerte-title-container">
            <img src={flagImage} alt="Drapeau Tunisien" className="title-flag-bg" />
            <img src={heroImage} alt="Bizerte - Perle du Nord" className="title-hero-img" />
            <div className="title-overlay">
              <h1>Bizerte</h1>
              <p>Perle du nord tunisien</p>
            </div>
          </div>
        </div>
      </div>

      {/* Attractions Section */}
      <section className="attractions-section">
        <div className="container">
          <div className="section-header">
            <h2>Sites Incontournables</h2>
            <p>Entre patrimoine maritime et beauts naturelles</p>
          </div>

          <div className="attractions-grid">
            {attractions.map((attraction, index) => (
              <div key={index} className="attraction-card">
                <div className="attraction-image">
                  <img src={attraction.image} alt={attraction.name} />
                  <div className="attraction-overlay">
                    <h3>{attraction.name}</h3>
                  </div>
                </div>
                <div className="attraction-content">
                  <p>{attraction.description}</p>
                  <div className="highlights">
                    <h4>Points forts :</h4>
                    <ul>
                      {attraction.highlights.map((highlight, idx) => (
                        <li key={idx}> {highlight}</li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Practical Info Section */}
      <section className="practical-info">
        <div className="container">
          <h2>Informations Pratiques</h2>
          <div className="info-grid">
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Accs</h3>
              <ul>
                <li>65 km de Tunis (1h en voiture)</li>
                <li>Liaison ferroviaire depuis Tunis</li>
                <li>Bus rguliers SNTRI</li>
                <li>Aroport de Tunis-Carthage</li>
              </ul>
            </div>

            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Visites</h3>
              <ul>
                <li>Fort d'Espagne : 5 DT</li>
                <li>Vieux port : accs libre</li>
                <li>Tour du lac : gratuit</li>
                <li>Muse naval : 3 DT</li>
              </ul>
            </div>

            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Activits</h3>
              <ul>
                <li>Baignade et bronzage</li>
                <li>Sports nautiques</li>
                <li>Pche en mer</li>
                <li>Randonne autour du lac</li>
              </ul>
            </div>

            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Gastronomie</h3>
              <ul>
                <li>Restaurants du vieux port</li>
                <li>Spcialits : poissons grills</li>
                <li>Couscous aux fruits de mer</li>
                <li>Cafs traditionnels</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* Accommodation Section */}
      <section className="accommodation">
        <div className="container">
          <h2>Hbergement</h2>
          <div className="accommodation-grid">
            <div className="accommodation-card">
              <h3>Htels vue mer</h3>
              <p>tablissements modernes avec vue sur la Mditerrane et accs direct aux plages</p>
              <ul>
                <li>Villa Didon (luxe)</li>
                <li>Htels corniche</li>
                <li>Rsidences balnaires</li>
              </ul>
            </div>

            <div className="accommodation-card">
              <h3>Maisons d'htes</h3>
              <p>Hbergements authentiques dans le centre historique prs du vieux port</p>
              <ul>
                <li>Dar Bizerte</li>
                <li>Maisons traditionnelles</li>
                <li>Chambres chez l'habitant</li>
              </ul>
            </div>

            <div className="accommodation-card">
              <h3>Campings</h3>
              <p>Options conomiques pour profiter de la nature prs du lac et des plages</p>
              <ul>
                <li>Camping lac de Bizerte</li>
                <li>Aires de camping-car</li>
                <li>Bivouac autoris</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="cta-section">
        <div className="container">
          <div className="cta-content">
            <h2>Explorez la perle du nord</h2>
            <p>Dcouvrez Bizerte, entre authenticit maritime et splendeurs naturelles</p>
            <div className="cta-buttons">
              <a href="#contact" className="btn btn-primary">Rserver votre sjour</a>
              <Link to="/destinations" className="btn btn-secondary">Autres destinations</Link>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default BizertePage;
