import React from 'react';
import { Link } from 'react-router-dom';
import './SoussePage.css';
import sousseImage from '../assets/sousse.jpg';
import medinaImage from '../assets/medinasousse.jpg';
import ribatImage from '../assets/ribatsousse.webp';
import cornichImage from '../assets/cornichesousse.jpg';
import kantaouiImage from '../assets/portkantaouisousse.jpg';
import mallImage from '../assets/mallofsousse.jpg';
import flagImage from '../assets/Flag.png';

const SoussePage: React.FC = () => {
  const attractions = [
    {
      id: 1,
      name: "Mdina de Sousse",
      description: "Centre historique class UNESCO, joyau architectural des dynasties aghlabide et fatimide",
      image: medinaImage,
      highlights: [
        "Patrimoine UNESCO depuis 1988",
        "Architecture islamique authentique",
        "Souks traditionnels anims",
        "Remparts du 9me sicle"
      ],
      history: "Fonde au 9me sicle, la mdina de Sousse est un exemple exceptionnel d'architecture militaire et religieuse de l'art arabo-musulman.",
      visitInfo: "Ouverte tous les jours. Visite guide recommande pour dcouvrir l'histoire."
    },
    {
      id: 2,
      name: "Ribat de Sousse",
      description: "Monastre-forteresse du 8me sicle, monument emblmatique de l'architecture militaire islamique",
      image: ribatImage,
      highlights: [
        "Construit en 821 aprs J.-C.",
        "Tour de guet de 30 mtres",
        "Architecture militaire unique",
        "Vue panoramique sur la ville"
      ],
      history: "Le Ribat de Sousse est l'un des mieux conservs au monde. Ces monastres-forteresses servaient  la fois de lieux de prire et de dfense.",
      visitInfo: "Entre gratuite. Monte au sommet de la tour recommande pour la vue."
    },
    {
      id: 3,
      name: "Corniche de Sousse",
      description: "Boulevard maritime anim, cur de la vie nocturne et des loisirs balnaires",
      image: cornichImage,
      highlights: [
        "Promenade en bord de mer",
        "Restaurants et cafs",
        "Vie nocturne anime",
        "Plages urbaines"
      ],
      history: "La corniche moderne de Sousse s'tend sur plusieurs kilomtres et constitue le centre nvralgique du tourisme balnaire tunisien.",
      visitInfo: "Accessible 24h/24. Nombreuses activits de jour comme de nuit."
    },
    {
      id: 4,
      name: "Port El Kantaoui",
      description: "Marina de luxe aux influences andalouses, station balnaire haut de gamme",
      image: kantaouiImage,
      highlights: [
        "Architecture andalouse moderne",
        "Marina de 340 places",
        "Golf 36 trous",
        "Complexes hteliers de luxe"
      ],
      history: "Cr dans les annes 1980, Port El Kantaoui est une station touristique intgre conue dans un style architectural andalou-mauresque.",
      visitInfo: "Accs libre  la marina. Nombreuses activits nautiques et terrestres."
    }
  ];

  return (
    <div className="sousse-page">
      {/* Hero Section */}
      <div className="page-header">
        <div className="container">
          <div className="sousse-title-container" style={{
            backgroundImage: `linear-gradient(rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.7)), url(${cornichImage})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center'
          }}>
            <img src={flagImage} alt="Drapeau Tunisien" className="title-flag-bg" />
            <div className="title-overlay">
              <h1>Sousse</h1>
              <p>Perle du Sahel tunisien, station balnaire alliant patrimoine historique et plaisirs modernes</p>
            </div>
          </div>
        </div>
      </div>

      {/* Introduction */}
      <section className="sousse-intro section-padding">
        <div className="container">
          <div className="intro-content">
            <div className="intro-text">
              <h2>Perle du Sahel Tunisien</h2>
              <p>
                Sousse, troisime ville de Tunisie, est une destination qui allie parfaitement 
                patrimoine historique et tourisme balnaire moderne. Surnomme "la perle du Sahel", 
                elle sduit par sa mdina classe UNESCO, ses plages dores et sa vie nocturne anime.
              </p>
              <p>
                De son ribat millnaire  ses complexes hteliers ultramodernes, en passant 
                par sa corniche anime et la luxueuse marina de Port El Kantaoui, Sousse 
                offre une exprience complte entre culture, dtente et divertissement. 
                C'est la destination idale pour dcouvrir l'art de vivre tunisien.
              </p>
            </div>
            <div className="intro-flag">
              <img src={flagImage} alt="Drapeau Tunisien" className="tunisia-flag" />
            </div>
          </div>
        </div>
      </section>

      {/* Attractions principales */}
      <section className="attractions section-padding">
        <div className="container">
          <div className="section-header text-center">
            <h2>Sites Incontournables</h2>
            <p>Dcouvrez les trsors historiques et balnaires de Sousse</p>
          </div>

          <div className="attractions-grid">
            {attractions.map((attraction, index) => (
              <div key={attraction.id} className="attraction-card" style={{ animationDelay: `${index * 0.2}s` }}>
                <div className="card-image">
                  <img src={attraction.image} alt={attraction.name} />
                  <div className="card-overlay">
                    <h3>{attraction.name}</h3>
                  </div>
                </div>
                
                <div className="card-content">
                  <p className="description">{attraction.description}</p>
                  
                  <h4>Points forts</h4>
                  <ul className="highlights-list">
                    {attraction.highlights.map((highlight, idx) => (
                      <li key={idx}>{highlight}</li>
                    ))}
                  </ul>
                  
                  <h4>Histoire</h4>
                  <p className="history-text">{attraction.history}</p>
                  
                  <h4>Informations pratiques</h4>
                  <p className="visit-info">{attraction.visitInfo}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Informations pratiques */}
      <section className="practical-info section-padding">
        <div className="container">
          <h2>Prparez votre sjour</h2>
          <div className="info-grid">
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Comment s'y rendre</h3>
              <ul>
                <li>140 km de Tunis (1h30 de route)</li>
                <li>Aroport Monastir  20 km</li>
                <li>Train direct depuis Tunis</li>
                <li>Nombreuses liaisons bus</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Meilleure priode</h3>
              <ul>
                <li>Mai-octobre : saison balnaire</li>
                <li>Avril et novembre : temps idal</li>
                <li>Hiver : doux, parfait pour visites</li>
                <li>300 jours de soleil par an</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Activits</h3>
              <ul>
                <li>Farniente sur les plages</li>
                <li>Sports nautiques varis</li>
                <li>Golf  Port El Kantaoui</li>
                <li>Vie nocturne anime</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Billets et tarifs</h3>
              <ul>
                <li>Mdina : Accs libre</li>
                <li>Ribat : Entre gratuite</li>
                <li>Muses : 5-8 TND</li>
                <li>Plages : Accs libre</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* Call to action */}
      <section className="cta-section section-padding">
        <div className="container text-center">
          <h2>Dcouvrez la perle du Sahel</h2>
          <p>Vivez l'exprience unique de Sousse entre mer et histoire</p>
          <div className="cta-buttons">
            <Link to="/destinations" className="btn btn-primary">
              Autres destinations
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
};

export default SoussePage;
