import React from 'react';
import { Link } from 'react-router-dom';
import './HammametPage.css';
import kasbahImage from '../assets/kasbahhammamet.jpg';
import beachImage from '../assets/hammametplage.webp';
import medinaImage from '../assets/medinahammamet.jpg';
import marinaImage from '../assets/marina hammamet.jpg';
import flagImage from '../assets/Flag.png';

const HammametPage: React.FC = () => {
  const attractions = [
    {
      id: 1,
      name: "Kasbah de Hammamet",
      description: "Forteresse du 15me sicle surplombant la mer, symbole historique de la ville",
      image: kasbahImage,
      highlights: [
        "Fortification du 15me sicle",
        "Vue panoramique sur la mer",
        "Architecture militaire authentique",
        "Muse des traditions populaires"
      ],
      history: "Construite au 15me sicle par les Hafsides, cette kasbah a dfendu Hammamet contre les invasions. Elle abrite aujourd'hui un muse des traditions.",
      visitInfo: "Ouverte tous les jours. Vue imprenable sur la mdina et la mer."
    },
    {
      id: 2,
      name: "Plages de Hammamet",
      description: "tendues de sable fin bordes d'eaux cristallines, paradis balnaire de la Tunisie",
      image: beachImage,
      highlights: [
        "Sable fin et dor",
        "Eaux cristallines",
        "Sports nautiques",
        "Restaurants en bord de mer"
      ],
      history: "Les plages de Hammamet ont fait la rputation touristique de la ville depuis les annes 1960, attirant des visiteurs du monde entier.",
      visitInfo: "Accs libre toute l'anne. Parasols et transats disponibles."
    },
    {
      id: 3,
      name: "Mdina de Hammamet",
      description: "Centre historique authentique avec ses ruelles blanches et ses souks colors",
      image: medinaImage,
      highlights: [
        "Architecture traditionnelle",
        "Souks artisanaux",
        "Cafs traditionnels",
        "Remparts historiques"
      ],
      history: "La mdina de Hammamet conserve son charme d'antan avec ses maisons blanches et bleues typiques du style architectural tunisien.",
      visitInfo: "Accessible librement. Idal pour le shopping et la dcouverte culturelle."
    },
    {
      id: 4,
      name: "Marina de Hammamet",
      description: "Port de plaisance moderne avec restaurants, boutiques et animations",
      image: marinaImage,
      highlights: [
        "Port de plaisance moderne",
        "Restaurants gastronomiques",
        "Boutiques de luxe",
        "Animations nocturnes"
      ],
      history: "Dveloppe dans les annes 2000, la marina a modernis l'offre touristique de Hammamet tout en respectant l'identit locale.",
      visitInfo: "Ouverte 24h/24. Nombreuses activits et restaurants."
    }
  ];

  return (
    <div className="hammamet-page">
      {/* Hero Section */}
      <div className="page-header">
        <div className="container">
          <div className="hammamet-title-container">
            <img src={flagImage} alt="Drapeau Tunisien" className="title-flag-bg" />
            <img src={beachImage} alt="Plages de Hammamet" className="title-hero-img" />
            <div className="title-overlay">
              <h1>Hammamet</h1>
              <p>Perle du Golfe de Tunis</p>
            </div>
          </div>
        </div>
      </div>

      {/* Introduction */}
      <section className="hammamet-intro section-padding">
        <div className="container">
          <div className="intro-content">
            <div className="intro-text">
              <h2>La Perle du Golfe de Tunis</h2>
              <p>
                Hammamet, surnomme "la perle du golfe de Tunis", est l'une des stations 
                balnaires les plus prises de Tunisie. Cette charmante ville ctire 
                allie parfaitement tradition et modernit, offrant aux visiteurs des plages 
                de sable fin, une mdina authentique et une infrastructure touristique de qualit.
              </p>
              <p>
                Rpute pour ses jardins d'agrumes parfums, ses eaux cristallines et 
                son patrimoine historique, Hammamet a su prserver son charme mditerranen 
                tout en dveloppant une offre touristique moderne. La ville est galement 
                clbre pour son festival international d'art dramatique et ses spas de thalassothrapie.
              </p>
            </div>
            <div className="intro-flag">
              <img src={flagImage} alt="Drapeau Tunisien" className="tunisia-flag" />
            </div>
          </div>
        </div>
      </section>

      {/* Attractions principales */}
      <section className="attractions section-padding">
        <div className="container">
          <div className="section-header text-center">
            <h2>Sites Incontournables</h2>
            <p>Dcouvrez les merveilles balnaires et historiques de Hammamet</p>
          </div>

          <div className="attractions-grid">
            {attractions.map((attraction, index) => (
              <div key={attraction.id} className="attraction-card" style={{ animationDelay: `${index * 0.2}s` }}>
                <div className="card-image">
                  <img src={attraction.image} alt={attraction.name} />
                  <div className="card-overlay">
                    <h3>{attraction.name}</h3>
                  </div>
                </div>
                
                <div className="card-content">
                  <p className="description">{attraction.description}</p>
                  
                  <h4>Points forts</h4>
                  <ul className="highlights-list">
                    {attraction.highlights.map((highlight, idx) => (
                      <li key={idx}>{highlight}</li>
                    ))}
                  </ul>
                  
                  <h4>Histoire</h4>
                  <p className="history-text">{attraction.history}</p>
                  
                  <h4>Informations pratiques</h4>
                  <p className="visit-info">{attraction.visitInfo}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Informations pratiques */}
      <section className="practical-info section-padding">
        <div className="container">
          <h2>Prparez votre visite</h2>
          <div className="info-grid">
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Comment s'y rendre</h3>
              <ul>
                <li>60 km de Tunis (1h de route)</li>
                <li>15 km de l'aroport Enfidha</li>
                <li>Liaisons bus et louages</li>
                <li>Train jusqu' Bir Bouregba puis bus</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Saison balnaire</h3>
              <ul>
                <li>Mai  octobre (mer chaude)</li>
                <li>Juin-septembre (haute saison)</li>
                <li>Avril et novembre (doux)</li>
                <li>Hiver : thalassothrapie</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Activits</h3>
              <ul>
                <li>Sports nautiques</li>
                <li>Golf (parcours 18 trous)</li>
                <li>Thalassothrapie</li>
                <li>Excursions en mer</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Billets et tarifs</h3>
              <ul>
                <li>Kasbah : Entre gratuite</li>
                <li>Plages : Accs libre</li>
                <li>Marina : Accs libre</li>
                <li>Muses : 2-5 TND</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* Call to action */}
      <section className="cta-section section-padding">
        <div className="container text-center">
          <h2>Planifiez votre sjour  Hammamet</h2>
          <p>Dcouvrez la magie de la perle du golfe de Tunis</p>
          <div className="cta-buttons">
            <Link to="/destinations" className="btn btn-primary">
              Autres destinations
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
};

export default HammametPage;
