import React from 'react';
import { Link } from 'react-router-dom';
import './DjerbaPage.css';
import djerbaImage from '../assets/djerba.jpg';
import plageImage from '../assets/djerbaplage.avif';
// @ts-ignore
import medinaImage from '../assets/medina djerba.jpg';
import djerbahoodImage from '../assets/djerbahood.webp';
import crocodileImage from '../assets/parccrocodiledjerba.webp';
import flagImage from '../assets/Flag.png';

const DjerbaPage: React.FC = () => {
  const attractions = [
    {
      id: 1,
      name: "Plages de Djerba",
      description: "tendues de sable fin aux eaux turquoise, paradis balnaire de la Mditerrane",
      image: plageImage,
      highlights: [
        "14 km de plages de sable fin",
        "Eaux turquoise et chaudes",
        "Sports nautiques varis",
        "Complexes hteliers de luxe"
      ],
      history: "Les plages de Djerba ont fait la rputation touristique de l'le depuis les annes 1960, attirant des visiteurs du monde entier pour leur beaut naturelle.",
      visitInfo: "Accs libre toute l'anne. Nombreuses activits nautiques disponibles."
    },
    {
      id: 2,
      name: "Mdina d'Houmt Souk",
      description: "Centre historique authentique de Djerba avec ses souks colors et son architecture traditionnelle",
      image: medinaImage,
      highlights: [
        "Souks traditionnels authentiques",
        "Architecture berbre et arabe",
        "Artisanat local unique",
        "Caf des Nattes historique"
      ],
      history: "Houmt Souk, capitale de Djerba, conserve son charme d'antan avec ses ruelles blanches et ses souks anims depuis des sicles.",
      visitInfo: "Accessible librement. Idal pour le shopping et la dcouverte culturelle."
    },
    {
      id: 3,
      name: "Djerbahood",
      description: "Muse de street art  ciel ouvert, projet artistique unique transformant le village d'Erriadh",
      image: djerbahoodImage,
      highlights: [
        "Plus de 250 uvres murales",
        "Artistes internationaux",
        "Village d'Erriadh transform",
        "Art contemporain en plein air"
      ],
      history: "Lanc en 2014, Djerbahood a transform le village d'Erriadh en galerie d'art  ciel ouvert, mlangeant tradition et modernit.",
      visitInfo: "Visite libre du village. Respect des uvres et des habitants demand."
    },
    {
      id: 4,
      name: "Parc Crocodile Djerba",
      description: "Parc animalier unique abritant plus de 400 crocodiles du Nil dans un cadre naturel",
      image: crocodileImage,
      highlights: [
        "Plus de 400 crocodiles du Nil",
        "Spectacles de nourrissage",
        "Jardin botanique tropical",
        "Activits familiales"
      ],
      history: "Cr pour la prservation des crocodiles du Nil, ce parc unique en Tunisie offre une exprience ducative et ludique pour toute la famille.",
      visitInfo: "Ouvert tous les jours. Spectacles  heures fixes. Idal pour les familles."
    }
  ];

  return (
    <div className="djerba-page">
      {/* Hero Section */}
      <div className="page-header">
        <div className="container">
          <div className="djerba-title-container">
            <img src={flagImage} alt="Drapeau Tunisien" className="title-flag-bg" />
            <img src={djerbaImage} alt="le de Djerba" className="title-hero-img" />
            <div className="title-overlay">
              <h1>Djerba</h1>
              <p>L'le des Rves - Paradis mditerranen</p>
            </div>
          </div>
        </div>
      </div>

      {/* Introduction */}
      <section className="djerba-intro section-padding">
        <div className="container">
          <div className="intro-content">
            <div className="intro-text">
              <h2>L'le des Rves</h2>
              <p>
                Djerba, la plus grande le de Tunisie, est un vritable joyau de la Mditerrane. 
                Surnomme "l'le des rves", elle sduit par ses plages de sable fin, ses eaux 
                turquoise, son patrimoine culturel riche et sa douceur de vivre lgendaire.
              </p>
              <p>
                Terre de tolrance et de diversit, Djerba abrite une mosaque de cultures 
                et de traditions. De ses villages berbres authentiques  ses complexes 
                touristiques modernes, en passant par ses souks colors et son art de rue 
                contemporain, l'le offre une exprience unique entre tradition et modernit.
              </p>
            </div>
            <div className="intro-flag">
              <img src={flagImage} alt="Drapeau Tunisien" className="tunisia-flag" />
            </div>
          </div>
        </div>
      </section>

      {/* Attractions principales */}
      <section className="attractions section-padding">
        <div className="container">
          <div className="section-header text-center">
            <h2>Sites Incontournables</h2>
            <p>Dcouvrez les merveilles de l'le des rves</p>
          </div>

          <div className="attractions-grid">
            {attractions.map((attraction, index) => (
              <div key={attraction.id} className="attraction-card" style={{ animationDelay: `${index * 0.2}s` }}>
                <div className="card-image">
                  <img src={attraction.image} alt={attraction.name} />
                  <div className="card-overlay">
                    <h3>{attraction.name}</h3>
                  </div>
                </div>
                
                <div className="card-content">
                  <p className="description">{attraction.description}</p>
                  
                  <h4>Points forts</h4>
                  <ul className="highlights-list">
                    {attraction.highlights.map((highlight, idx) => (
                      <li key={idx}>{highlight}</li>
                    ))}
                  </ul>
                  
                  <h4>Histoire</h4>
                  <p className="history-text">{attraction.history}</p>
                  
                  <h4>Informations pratiques</h4>
                  <p className="visit-info">{attraction.visitInfo}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Informations pratiques */}
      <section className="practical-info section-padding">
        <div className="container">
          <h2>Prparez votre visite</h2>
          <div className="info-grid">
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Comment s'y rendre</h3>
              <ul>
                <li>Aroport Djerba-Zarzis (direct)</li>
                <li>520 km de Tunis (6h de route)</li>
                <li>Causeway reliant l'le au continent</li>
                <li>Ferry depuis Jorf (sud tunisien)</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Climat et saisons</h3>
              <ul>
                <li>Toute l'anne (climat doux)</li>
                <li>t : chaud et sec (28-35C)</li>
                <li>Hiver : doux et ensoleill (15-20C)</li>
                <li>330 jours de soleil par an</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Activits</h3>
              <ul>
                <li>Sports nautiques et plonge</li>
                <li>Quad et excursions 4x4</li>
                <li>Thalassothrapie et spa</li>
                <li>Festivals culturels</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Billets et tarifs</h3>
              <ul>
                <li>Plages : Accs libre</li>
                <li>Djerbahood : Visite gratuite</li>
                <li>Parc Crocodile : 15 TND</li>
                <li>Mdina : Accs libre</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* Call to action */}
      <section className="cta-section section-padding">
        <div className="container text-center">
          <h2>Vivez la magie de Djerba</h2>
          <p>Dcouvrez l'le des rves et ses merveilles infinies</p>
          <div className="cta-buttons">
            <Link to="/destinations" className="btn btn-primary">
              Autres destinations
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
};

export default DjerbaPage;
