import React from 'react';
import { useNavigate } from 'react-router-dom';
import './Experiences.css';

const Experiences: React.FC = () => {
  const navigate = useNavigate();

  const handleCategoryExplore = (categoryId: string) => {
    if (categoryId === 'beach') {
      navigate('/experiences/plages-cotes');
    } else if (categoryId === 'gastronomy') {
      navigate('/experiences/gastronomie');
    } else if (categoryId === 'desert') {
      navigate('/experiences/nature-decouverte');
    } else if (categoryId === 'wellness') {
      navigate('/experiences/bien-etre');
    } else if (categoryId === 'sports') {
      navigate('/experiences/sports-loisirs');
    } else if (categoryId === 'culture') {
      navigate('/experiences/medinas-patrimoine');
    }
    // Ici on peut ajouter d'autres navigations pour les autres catgories
  };

  const experienceCategories = [
    {
      id: 'beach',
      title: 'Plages & Ctes',
      subtitle: 'Vos vacances sur la cte mditerranenne',
      description: 'Des plages de sable fin aux eaux cristallines',
      icon: '',
      color: 'var(--tunisia-blue)',
      experiences: [
        'Plages dores de Hammamet',
        'Cte sauvage du Cap Bon', 
        'Lagunes de Djerba',
        'Thalassothrapie  Sousse'
      ],
      featured: true
    },
    {
      id: 'culture',
      title: 'Mdinas & Patrimoine',
      subtitle: 'Immersion dans l\'hritage culturel',
      description: 'Architecture islamique et traditions millnaires',
      icon: '',
      color: 'var(--tunisia-red)',
      experiences: [
        'Mdina de Tunis (UNESCO)',
        'Grande Mosque de Kairouan',
        'Site archologique de Carthage',
        'Amphithtre d\'El Jem'
      ]
    },
    {
      id: 'desert',
      title: 'Nature & Dcouverte',
      subtitle: 'Aventures dans le patrimoine rural',
      description: 'Du dsert aux oasis, paysages  couper le souffle',
      icon: '',
      color: 'var(--tunisia-gold)',
      experiences: [
        'Excursions dans le Sahara',
        'Oasis de montagne de Chebika',
        'Villages berbres authentiques',
        'Nuits sous les toiles'
      ]
    },
    {
      id: 'wellness',
      title: 'Bien-tre & Dtente',
      subtitle: 'Escapade revitalisante en Tunisie',
      description: 'Cures thermales, spas et centres de bien-tre',
      icon: '',
      color: 'var(--tunisia-sunset)',
      experiences: [
        'Centres de thalassothrapie',
        'Sources thermales de Korbous',
        'Hammams traditionnels',
        'Spas de luxe'
      ]
    },
    {
      id: 'gastronomy',
      title: 'Art de Vivre',
      subtitle: 'Gastronomie et traditions tunisiennes',
      description: 'Saveurs authentiques et savoir-faire culinaire',
      icon: '',
      color: 'var(--tunisia-olive)',
      experiences: [
        'Cours de cuisine traditionnelle',
        'Marchs aux pices de Tunis',
        'Dgustations d\'huile d\'olive',
        'Ptisseries orientales'
      ]
    },
    {
      id: 'sports',
      title: 'Sports & Loisirs',
      subtitle: 'Activits sportives et de loisirs',
      description: 'Golf, plonge et sports nautiques',
      icon: '',
      color: '#1976d2',
      experiences: [
        'Golf face  la mer',
        'Plonge sous-marine',
        'Sports nautiques',
        'Randonnes en montagne'
      ]
    }
  ];

  return (
    <section className="experiences section-padding" id="experiences">
      <div className="container">
        <div className="experiences-grid">
          {experienceCategories.map((category, index) => (
            <div 
              key={category.id}
              className="experience-category fade-in-up"
              style={{ animationDelay: `${index * 0.1}s` }}
            >
              <div className="category-header">
                <div 
                  className="category-icon"
                  style={{ backgroundColor: category.color }}
                >
                  {category.icon}
                </div>
                <div className="category-info">
                  <h3>{category.title}</h3>
                  <h4>{category.subtitle}</h4>
                  <p>{category.description}</p>
                </div>
              </div>
              
              <div className="category-experiences">
                <ul>
                  {category.experiences.map((experience, idx) => (
                    <li key={idx}>
                      <span className="experience-bullet"></span>
                      {experience}
                    </li>
                  ))}
                </ul>
              </div>
              
              <div className="category-cta">
                <button 
                  className="btn-discover"
                  style={{ color: category.color, borderColor: category.color }}
                  onClick={() => handleCategoryExplore(category.id)}
                >
                  Explorer
                  <span className="arrow"></span>
                </button>
              </div>
            </div>
          ))}
        </div>

        <div className="experiences-highlight">
          <div className="highlight-content">
            <div className="highlight-left">
              <h3>Qu'est-ce qui vous fait vibrer ?</h3>
              <p>
                Que vous recherchiez la dtente sur nos plages dores, l'aventure dans le dsert, 
                la dcouverte de notre patrimoine millnaire ou les plaisirs de la gastronomie tunisienne, 
                nous avons l'exprience parfaite pour vous.
              </p>
              <div className="highlight-stats">
                <div className="stat">
                  <span className="stat-number">150+</span>
                  <span className="stat-label">Expriences</span>
                </div>
                <div className="stat">
                  <span className="stat-number">50+</span>
                  <span className="stat-label">Destinations</span>
                </div>
                <div className="stat">
                  <span className="stat-number">24/7</span>
                  <span className="stat-label">Support</span>
                </div>
              </div>
            </div>
            
            <div className="highlight-right">
              <div className="experience-finder">
                <h4>Trouvez votre exprience idale</h4>
                <p>Dcouvrez la Tunisie selon vos passions</p>
                <div className="finder-options">
                  <button className="finder-btn active" onClick={() => handleCategoryExplore('beach')}> Plages</button>
                  <button className="finder-btn" onClick={() => handleCategoryExplore('culture')}> Culture</button>
                  <button className="finder-btn" onClick={() => handleCategoryExplore('desert')}> Aventure</button>
                  <button className="finder-btn" onClick={() => handleCategoryExplore('wellness')}> Bien-tre</button>
                  <button className="finder-btn" onClick={() => handleCategoryExplore('gastronomy')}> Gastronomie</button>
                  <button className="finder-btn" onClick={() => handleCategoryExplore('sports')}> Sports</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Experiences;
