import React from 'react';
import { Link } from 'react-router-dom';
import './TabarkaPage.css';
import fortImage from '../assets/forttabarka.jpg';
import neigeImage from '../assets/tabarkaneige.jpg';
import plageImage from '../assets/plagetabarka.webp';
import tabarkaImage from '../assets/tabarka.jpg';
import flagImage from '../assets/Flag.png';

const TabarkaPage: React.FC = () => {
  const attractions = [
    {
      id: 1,
      name: "Fort Gnois de Tabarka",
      description: "Forteresse du 16me sicle construite par les Gnois, emblme historique de la ville",
      image: fortImage,
      highlights: [
        "Architecture gnoise du 16me sicle",
        "Vue panoramique sur la mer",
        "Histoire des corsaires",
        "Site archologique"
      ],
      history: "Construit par les Gnois au 16me sicle, ce fort tmoigne de l'histoire commerciale de Tabarka et de sa position stratgique en Mditerrane.",
      visitInfo: "Ouvert tous les jours. Monte au sommet recommande pour la vue panoramique."
    },
    {
      id: 2,
      name: "Aiguilles de Tabarka",
      description: "Formations rocheuses spectaculaires mergeant de la mer Mditerrane",
      image: tabarkaImage,
      highlights: [
        "Rochers de grs rouge",
        "Formations gologiques uniques",
        "Spot de plonge rput",
        "Couchers de soleil magiques"
      ],
      history: "Ces formations rocheuses de grs rouge ont t sculptes par l'rosion marine au fil des millnaires, crant un paysage unique en Tunisie.",
      visitInfo: "Visibles depuis la cte. Excursions en bateau disponibles pour approcher les rochers."
    },
    {
      id: 3,
      name: "Plages de Tabarka",
      description: "Cte sauvage aux eaux cristallines, paradis des amateurs de nature",
      image: plageImage,
      highlights: [
        "Eaux cristallines",
        "Cte rocheuse prserve",
        "Plonge sous-marine",
        "Pche en mer"
      ],
      history: "Les plages de Tabarka offrent un cadre naturel prserv, loin du tourisme de masse, idal pour les amoureux de nature authentique.",
      visitInfo: "Accs libre. quipements de plonge disponibles en location."
    },
    {
      id: 4,
      name: "Montagnes de Kroumirie",
      description: "Rgion montagneuse verdoyante, seul endroit en Tunisie o il peut neiger",
      image: neigeImage,
      highlights: [
        "Forts de chnes-liges",
        "Climat mditerranen montagnard",
        "Randonnes en nature",
        "Neige en hiver (rare)"
      ],
      history: "La Kroumirie est une rgion montagneuse unique en Tunisie, connue pour ses forts denses et son climat particulier qui peut apporter de la neige en hiver.",
      visitInfo: "Idal pour la randonne. Meilleure priode : automne et printemps."
    }
  ];

  return (
    <div className="tabarka-page">
      {/* Hero Section */}
      <div className="page-header">
        <div className="container">
          <div className="tabarka-title-container">
            <img src={flagImage} alt="Drapeau Tunisien" className="title-flag-bg" />
            <img src={fortImage} alt="Fort Gnois de Tabarka" className="title-hero-img" />
            <div className="title-overlay">
              <h1>Tabarka</h1>
              <p>Perle du Nord-Ouest Tunisien</p>
            </div>
          </div>
        </div>
      </div>

      {/* Introduction */}
      <section className="tabarka-intro section-padding">
        <div className="container">
          <div className="intro-content">
            <div className="intro-text">
              <h2>La Perle du Nord-Ouest</h2>
              <p>
                Tabarka, situe dans le nord-ouest de la Tunisie, est une destination 
                unique qui allie mer Mditerrane et montagnes verdoyantes. Cette petite 
                ville ctire est clbre pour ses formations rocheuses spectaculaires, 
                son fort gnois historique et sa proximit avec les montagnes de Kroumirie.
              </p>
              <p>
                Rpute pour son festival de jazz annuel, ses eaux cristallines parfaites 
                pour la plonge et ses paysages naturels prservs, Tabarka offre une 
                exprience authentique loin des circuits touristiques traditionnels. 
                C'est galement le seul endroit en Tunisie o vous pourrez peut-tre voir de la neige !
              </p>
            </div>
            <div className="intro-flag">
              <img src={flagImage} alt="Drapeau Tunisien" className="tunisia-flag" />
            </div>
          </div>
        </div>
      </section>

      {/* Attractions principales */}
      <section className="attractions section-padding">
        <div className="container">
          <div className="section-header text-center">
            <h2>Sites Incontournables</h2>
            <p>Dcouvrez les merveilles naturelles et historiques de Tabarka</p>
          </div>

          <div className="attractions-grid">
            {attractions.map((attraction, index) => (
              <div key={attraction.id} className="attraction-card" style={{ animationDelay: `${index * 0.2}s` }}>
                <div className="card-image">
                  <img src={attraction.image} alt={attraction.name} />
                  <div className="card-overlay">
                    <h3>{attraction.name}</h3>
                  </div>
                </div>
                
                <div className="card-content">
                  <p className="description">{attraction.description}</p>
                  
                  <h4>Points forts</h4>
                  <ul className="highlights-list">
                    {attraction.highlights.map((highlight, idx) => (
                      <li key={idx}>{highlight}</li>
                    ))}
                  </ul>
                  
                  <h4>Histoire</h4>
                  <p className="history-text">{attraction.history}</p>
                  
                  <h4>Informations pratiques</h4>
                  <p className="visit-info">{attraction.visitInfo}</p>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Informations pratiques */}
      <section className="practical-info section-padding">
        <div className="container">
          <h2>Prparez votre visite</h2>
          <div className="info-grid">
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Comment s'y rendre</h3>
              <ul>
                <li>170 km de Tunis (2h30 de route)</li>
                <li>80 km de Bja (1h30)</li>
                <li>Bus depuis Tunis et Bja</li>
                <li>Route pittoresque par la montagne</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Climat et saisons</h3>
              <ul>
                <li>t : chaud et sec (25-30C)</li>
                <li>Hiver : doux et humide (10-16C)</li>
                <li>Printemps/Automne : idal</li>
                <li>Possibilit de neige en janvier</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Activits</h3>
              <ul>
                <li>Plonge sous-marine</li>
                <li>Randonnes en Kroumirie</li>
                <li>Festival de jazz (juillet)</li>
                <li>Pche en mer</li>
              </ul>
            </div>
            
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Billets et tarifs</h3>
              <ul>
                <li>Fort gnois : Entre gratuite</li>
                <li>Plages : Accs libre</li>
                <li>Excursions bateau : 15-25 TND</li>
                <li>Plonge : 40-60 TND</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* Call to action */}
      <section className="cta-section section-padding">
        <div className="container text-center">
          <h2>Explorez la beaut sauvage de Tabarka</h2>
          <p>Dcouvrez un joyau cach du nord tunisien</p>
          <div className="cta-buttons">
            <Link to="/destinations" className="btn btn-outline">
              Autres destinations
            </Link>
            <Link to="/contact" className="btn btn-primary">
              Planifier votre voyage
            </Link>
          </div>
        </div>
      </section>
    </div>
  );
};

export default TabarkaPage;
