import React from 'react';
import { Link } from 'react-router-dom';
import './MahdiaPage.css';
// @ts-ignore
import heroImage from '../assets/mahdiacolissum.jpg';
// @ts-ignore
import grandemosqueeImage from '../assets/mahdia-grande-mosquee.jpg';
// @ts-ignore
import borjImage from '../assets/Borj_El_KebirMahdia.jpg';
// @ts-ignore
import skifaImage from '../assets/skifakahlamahdia.jpg';
// @ts-ignore
import ruinesImage from '../assets/ruindoormahdia.jpg';
// @ts-ignore
import plageMahdiaImage from '../assets/plagemahdia.jpg';
import flagImage from '../assets/Flag.png';

const MahdiaPage: React.FC = () => {
  const attractions = [
    {
      name: "Grande Mosque de Mahdia",
      description: "Construite au Xe sicle par le fondateur de la dynastie fatimide, cette mosque historique est un joyau de l'architecture islamique avec son minaret distinctif et sa cour pave de marbre.",
      image: grandemosqueeImage,
      highlights: ["Architecture fatimide", "Minaret historique", "Cour en marbre", "Monument class"]
    },
    {
      name: "Borj El Kebir",
      description: "Forteresse imposante du XVIe sicle qui domine la presqu'le de Mahdia, offrant une vue panoramique sur la Mditerrane et tmoignant de l'importance stratgique de la ville.",
      image: borjImage,
      highlights: ["Forteresse ottomane", "Vue panoramique", "Architecture militaire", "Muse historique"]
    },
    {
      name: "Skifa Kahla",
      description: "Porte monumentale fatimide du Xe sicle, vestige des anciens remparts de la ville. Cette porte noire ('Skifa Kahla' en arabe) est l'un des monuments les plus emblmatiques de Mahdia.",
      image: skifaImage,
      highlights: ["Monument fatimide", "Porte historique", "Architecture unique", "Patrimoine UNESCO"]
    },
    {
      name: "Plages de Mahdia",
      description: "Magnifiques plages de sable fin s'tendant sur plusieurs kilomtres, rputes pour leurs eaux cristallines et leur cadre paisible, parfaites pour la dtente et les sports nautiques.",
      image: plageMahdiaImage,
      highlights: ["Sable fin dor", "Eaux cristallines", "Sports nautiques", "Cadre paisible"]
    },
    {
      name: "Vestiges Antiques",
      description: "Sites archologiques tmoignant du riche pass de Mahdia, avec des vestiges romains et byzantins qui racontent l'histoire millnaire de cette cit portuaire.",
      image: ruinesImage,
      highlights: ["Vestiges romains", "Sites archologiques", "Histoire millnaire", "Patrimoine antique"]
    },
    {
      name: "Amphithtre de Mahdia",
      description: "Magnifique amphithtre antique offrant un cadre spectaculaire avec vue sur la Mditerrane. Ce monument historique tmoigne de la grandeur de l'poque romaine  Mahdia.",
      image: heroImage,
      highlights: ["Architecture antique", "Vue panoramique", "Spectacles historiques", "Monument emblmatique"]
    }
  ];

  return (
    <div className="mahdia-page">
      {/* Hero Section */}
      <div className="page-header">
        <div className="container">
          <div className="mahdia-title-container">
            <img src={flagImage} alt="Drapeau Tunisien" className="title-flag-bg" />
            <img src={heroImage} alt="Amphithtre de Mahdia" className="title-hero-img" />
            <div className="title-overlay">
              <h1>Mahdia</h1>
              <p>Perle du Sahel tunisien, berceau de la dynastie fatimide</p>
            </div>
          </div>
        </div>
      </div>

      {/* Attractions Section */}
      <section className="attractions-section">
        <div className="container">
          <div className="section-header">
            <h2>Sites Incontournables</h2>
            <p>Du patrimoine fatimide aux plages dores du Sahel</p>
          </div>

          <div className="attractions-grid">
            {attractions.map((attraction, index) => (
              <div key={index} className="attraction-card">
                <div className="attraction-image">
                  <img src={attraction.image} alt={attraction.name} />
                  <div className="attraction-overlay">
                    <h3>{attraction.name}</h3>
                  </div>
                </div>
                <div className="attraction-content">
                  <p>{attraction.description}</p>
                  <div className="highlights">
                    <h4>Points forts :</h4>
                    <ul>
                      {attraction.highlights.map((highlight, idx) => (
                        <li key={idx}> {highlight}</li>
                      ))}
                    </ul>
                  </div>
                </div>
              </div>
            ))}
          </div>
        </div>
      </section>

      {/* Practical Info Section */}
      <section className="practical-info">
        <div className="container">
          <h2>Informations Pratiques</h2>
          <div className="info-grid">
            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Accs</h3>
              <ul>
                <li>205 km de Tunis (2h30 en voiture)</li>
                <li>60 km de Sousse (1h en voiture)</li>
                <li>Bus rguliers depuis Tunis et Sousse</li>
                <li>Aroport de Monastir (45 min)</li>
              </ul>
            </div>

            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Visites</h3>
              <ul>
                <li>Grande Mosque : 3 DT</li>
                <li>Borj El Kebir : 5 DT</li>
                <li>Skifa Kahla : 2 DT</li>
                <li>Sites archologiques : 4 DT</li>
              </ul>
            </div>

            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Activits</h3>
              <ul>
                <li>Plages et baignade</li>
                <li>Plonge sous-marine</li>
                <li>Visites historiques guides</li>
                <li>Promenades dans la mdina</li>
              </ul>
            </div>

            <div className="info-card">
              <div className="info-icon"></div>
              <h3>Gastronomie</h3>
              <ul>
                <li>Poissons frais du jour</li>
                <li>Spcialits : daurade grille</li>
                <li>Restaurants face  la mer</li>
                <li>Ptisseries traditionnelles</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* Accommodation Section */}
      <section className="accommodation">
        <div className="container">
          <h2>Hbergement</h2>
          <div className="accommodation-grid">
            <div className="accommodation-card">
              <h3>Htels balnaires</h3>
              <p>tablissements en bord de mer avec accs direct aux plages et services spa</p>
              <ul>
                <li>Htels 4-5 toiles</li>
                <li>Centres de thalassothrapie</li>
                <li>Clubs de plage privs</li>
              </ul>
            </div>

            <div className="accommodation-card">
              <h3>Maisons d'htes</h3>
              <p>Hbergements authentiques dans la mdina historique prs des monuments</p>
              <ul>
                <li>Demeures traditionnelles</li>
                <li>Riads restaurs</li>
                <li>Chambres chez l'habitant</li>
              </ul>
            </div>

            <div className="accommodation-card">
              <h3>Rsidences</h3>
              <p>Appartements et villas avec vue sur mer pour sjours en famille</p>
              <ul>
                <li>Villas avec piscine</li>
                <li>Rsidences scurises</li>
                <li>Locations saisonnires</li>
              </ul>
            </div>
          </div>
        </div>
      </section>

      {/* CTA Section */}
      <section className="cta-section">
        <div className="container">
          <div className="cta-content">
            <h2>Explorez la perle du Sahel</h2>
            <p>Dcouvrez Mahdia, entre histoire fatimide et beauts naturelles</p>
            <div className="cta-buttons">
              <Link to="/destinations" className="btn btn-primary">Autres destinations</Link>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};

export default MahdiaPage;
